/**
 * Electron API Emulator
 * 
 * En emulator som erstter Electron IPC med webblsarens API:er.
 * Detta gr att befintlig kod som anvnder window.electronAPI kan fortstta
 * fungera i en webblsarkontext utan Electron.
 */
import { 
  readColumnMappings, 
  saveColumnMappings,
  handleFileUpload,
  downloadFile,
  downloadExcel,
  openExternalLink
} from './webStorageService';

/**
 * Mockad konfigurationsfil fr att simulera Electron-filtkomst
 */
const MOCK_CONFIG = {
  'config/column-mappings.json': JSON.stringify({
    // Innehllet kan lsas in frn localStorage vid initialisering
  })
};

/**
 * Initierar och exponerar Electron API-emulatorn
 */
export function initElectronApiEmulator() {
  if (typeof window === 'undefined') return; // Skip on server-side rendering
  
  // Skapa mockad electronAPI om den inte redan finns
  if (!window.electronAPI) {
    console.log('Initierar Electron API-emulator fr webben');
    
    // Asynkron metod fr att lsa filer
    const readFile = async (filePath, options = {}) => {
      console.log(`Mock readFile: ${filePath}`);
      
      // Specialfall fr kolumnmappningar
      if (filePath === 'config/column-mappings.json') {
        const mappings = await readColumnMappings();
        return JSON.stringify(mappings);
      }
      
      // Om vi har en mockat svar fr denna filvg
      if (MOCK_CONFIG[filePath]) {
        return MOCK_CONFIG[filePath];
      }
      
      throw new Error(`Filen finns inte: ${filePath}`);
    };
    
    // Asynkron metod fr att skriva filer
    const writeFile = async (filePath, content) => {
      console.log(`Mock writeFile: ${filePath}`);
      
      // Specialfall fr kolumnmappningar
      if (filePath === 'config/column-mappings.json') {
        try {
          const mappings = JSON.parse(content);
          await saveColumnMappings(mappings);
          return true;
        } catch (error) {
          console.error('Fel vid skrivning av kolumnmappningar:', error);
          throw error;
        }
      }
      
      // Uppdatera vr mockade konfiguration
      MOCK_CONFIG[filePath] = content;
      return true;
    };
    
    // Skapa mockad API
    window.electronAPI = {
      // Filsystem
      readFile,
      writeFile,
      
      // Extern lnkppning
      openExternalLink: (url) => openExternalLink(url),
      
      // Exportfunktioner
      exportToExcel: async (data, filename) => {
        return await downloadExcel(data, filename);
      },
      
      exportToCSV: async (data, filename) => {
        // Anvnd papaparse fr att konvertera data till CSV
        const Papa = await import('papaparse');
        const csvContent = Papa.unparse(data);
        return downloadFile(csvContent, filename, 'text/csv');
      },
      
      // Dialog fr att spara filer
      showSaveDialog: async (options) => {
        console.log('Mock showSaveDialog:', options);
        // I webblsaren kan vi inte visa en "save dialog", s vi returnerar
        // ett frvalt filnamn frn options
        return {
          canceled: false,
          filePath: options.defaultPath || 'nedladdad-fil.csv'
        };
      }
    };
    
    console.log('Electron API-emulator initierad');
  }
}

// Exportera en funktion fr att ladda om/uppdatera mocks om det behvs
export function updateMockConfig(key, value) {
  MOCK_CONFIG[key] = value;
}

// Exportera en helper-funktion fr att f en modifierad mockkonfiguration fr tester
export function getMockConfig() {
  return { ...MOCK_CONFIG };
}